.date-end,
.date-end-2 {
  align-items: center;
  background-color: var(--matisse);
  border-radius: 31px;
  display: flex;
  height: 50px;
  justify-content: flex-end;
  margin-left: 12px;
  min-width: 202px;
  padding: 1px 27.2px;
}

.date-end-1,
.date-end-3 {
  align-self: flex-end;
  letter-spacing: 0;
  line-height: 30px;
  min-height: 38px;
  min-width: 118px;
  white-space: nowrap;
}

.icon-calendar-2,
.icon-calendar-3 {
  height: 18px;
  margin-left: 10px;
  object-fit: cover;
  width: 16px;
}

