.filter-1 {
  align-items: flex-start;
  display: flex;
  min-width: 133px;
}

.overlap-group2 {
  height: 136px;
  position: relative;
  width: 133px;
}

.rectangle-3 {
  background-color: var(--matisse);
  box-shadow: 0px 3px 6px #00000029;
  height: 50px;
  left: 0;
  position: absolute;
  top: 0;
  width: 133px;
}

.arrow-2 {
  height: 9px;
  left: 114px;
  object-fit: cover;
  position: absolute;
  top: 21px;
  width: 14px;
}

.filters {
  left: 10px;
  letter-spacing: 0;
  line-height: 30px;
  position: absolute;
  top: 10px;
  white-space: nowrap;
}

.filter-dropdown {
  align-items: flex-start;
  background-color: var(--matisse);
  box-shadow: 0px 3px 6px #00000029;
  display: flex;
  flex-direction: column;
  left: 0;
  min-height: 92px;
  padding: 11.5px 13px;
  position: absolute;
  top: 44px;
  width: 133px;
}

.value {
  letter-spacing: 0;
  line-height: 24px;
  margin-top: 2px;
  min-height: 31px;
  white-space: nowrap;
}

.weight {
  letter-spacing: 0;
  line-height: 24px;
  margin-top: 5px;
  min-height: 31px;
  white-space: nowrap;
}

