.filter {
  align-items: center;
  background-color: var(--matisse);
  box-shadow: 0px 3px 6px #00000029;
  display: flex;
  height: 50px;
  justify-content: flex-end;
  min-width: 133px;
  padding: 2px 6.0px;
}

.title {
  align-self: flex-end;
  letter-spacing: 0;
  line-height: 30px;
  min-height: 38px;
  min-width: 71px;
  white-space: nowrap;
}

.arrow {
  height: 9px;
  margin-left: 33px;
  margin-top: 1.0px;
  object-fit: cover;
  width: 14px;
}

