:root { 
  --black: #000000;
  --matisse: #195e98;
  --romance: #fffdfd;
  --screamin-green: #6cff93;
  --white: #ffffff;
 
  --font-size-m: 30px;
  --font-size-s: 24px;
 
  --font-family-applesd_gothic_neo-regular: "AppleSD Gothic Neo-Regular", Helvetica;
}
.applesdgothicneo-regular-normal-romance-30px {
  color: var(--romance);
  font-family: var(--font-family-applesd_gothic_neo-regular);
  font-size: var(--font-size-m);
  font-style: normal;
  font-weight: 400;
}

.applesdgothicneo-regular-normal-romance-24px {
  color: var(--romance);
  font-family: var(--font-family-applesd_gothic_neo-regular);
  font-size: var(--font-size-s);
  font-style: normal;
  font-weight: 400;
}

.border-3px-black {
  border: 3px solid var(--black);
}
